.knowledge-base {
  padding: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  background-color: #f7f9fb;
  min-height: 90vh;
}

/* Ê®ôÈ°å */
.knowledge-base h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

/* ÊéßÂà∂ÂçÄÔºöÂàÜÈ°û + ÊêúÁ¥¢ + ÊéíÂ∫è */
.knowledge-controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-bottom: 20px;
}

.search-box,
.sort-dropdown,
.category-dropdown {
  padding: 10px 14px;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: white;
  transition: all 0.2s ease-in-out;
}

.search-box:focus,
.sort-dropdown:focus,
.category-dropdown:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

/* Ë≥áÊñôË°®Ê†º */
.knowledge-table {
  width: 90%;
  margin: 0 auto;
  border-collapse: collapse;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
}

.knowledge-table th,
.knowledge-table td {
  padding: 14px 10px;
  text-align: center;
  border-bottom: 1px solid #eaeaea;
  transition: background-color 0.2s ease;
}

.knowledge-table th {
  background-color: #007bff;
  color: white;
  font-weight: 600;
  font-size: 15px;
}

.knowledge-table tr:nth-child(even) {
  background-color: #f4f8ff;
}

.knowledge-table tr:hover {
  background-color: #e9f3ff;
}

/* ÈüøÊáâÂºèÔºàÊâãÊ©üÊôÇËá™ÂãïÊèõË°åÔºâ */
@media (max-width: 768px) {
  .knowledge-controls {
    flex-direction: column;
    align-items: center;
  }

  .knowledge-table {
    width: 100%;
    font-size: 14px;
  }

  .knowledge-table th,
  .knowledge-table td {
    padding: 10px 6px;
  }
}

/* Modal Ë¶ñÁ™ó */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal-content {
  background: white;
  padding: 20px 30px;
  border-radius: 12px;
  max-width: 600px;
  width: 90%;
  text-align: left;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
}

.close-btn {
  margin-top: 15px;
  padding: 8px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

