.data-analysis {
  padding: 20px;
  font-family: Arial, sans-serif;
  text-align: center;
  color: #333;
  background-color: #f7f9fb;
}
/*ÂúñË°®Â∞çÈΩä*/
.analysis-section {
  width: 90%;
  max-width: 1000px;
  margin: 40px auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center; /* ‚úÖ Ê®ôÈ°åËàáÂúñË°®ÈÉΩÁΩÆ‰∏≠ */
}

.analysis-section h2 {
  text-align: left;
  font-size: 20px;
  margin-bottom: 15px;
  color: #333;
}

/* Â∞èÊ®ôÈ°åËàáÈñìË∑ù */
.data-analysis h2 {
  margin-top: 40px;
  color: #007bff;
}

/* ÊôÇÈñìÁØ©ÈÅ∏ÊéßÂà∂È†Ö */
.time-filter {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

.time-filter select {
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 14px;
}

/* Ê¥ªË∫ç‰ΩøÁî®ËÄÖË°®Ê†º */
.active-users-table {
  width: 60%;
  margin: 20px auto;
  border-collapse: collapse;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.active-users-table th,
.active-users-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: center;
}

.active-users-table th {
  background-color: #007bff;
  color: white;
}

.active-users-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.active-users-table tr:hover {
  background-color: #e0f0ff;
  transition: background-color 0.3s;
}
